import Footer from "../components/Footer";
import salonjpg from "../images/salon-szkolenia/salon.jpg";
import salonwebp from "../images/salon-szkolenia/salon.webp";
import salon1000 from "../images/salon-szkolenia/salon.jpg";
import salon1000webp from "../images/salon-szkolenia/salon.webp";

export default function Training() {
  return (
    <section className="wrap-training">
      <article className="training-starter component">
        <div className="content-container boxx">
          <h1>Szkolenia</h1>
          <p className="contact-text">
            Chcesz zdoby lub poszerzy swoj wiedz i umiejtnoci z zakresu
            nowoczesnej kosmetologii, podologii i masau? Zapraszam do udziau w
            szkoleniach, kursach i seminariach organizowanych przez moje Forum
            Szkoleniowe. Spotkania prowadzone s pod okiem dowiadczonych
            specjalistw w swojej dziedzinie. Uzyskasz na nich wyczerpujc
            wiedz teoretyczn niezbdn do bezpiecznego wykonania zabiegu oraz
            wywiczysz nowo nabyte umiejtnoci. Zajcia organizowane s w
            sprzyjajcej rozwojowi atmosferze. Po zakoczeniu uzyskasz
            Certyfikat powiadczajcy nabyte umiejtnoci.
          </p>
        </div>
        <div className="image-container boxx">
          <picture>
            <source
              media="(max-width: 1000px)"
              type="image/jpg"
              srcset={salon1000}
            />
            <source
              media="(min-width: 1001px)"
              type="image/jpg"
              srcset={salonjpg}
            />
            <source
              media="(max-width: 1000px)"
              type="image/webp"
              srcset={salon1000webp}
            />
            <source
              media="(min-width: 1001px)"
              type="image/webp"
              srcset={salonwebp}
            />
            <img alt="szkolenie" />
          </picture>
        </div>
      </article>
      <article className="training-contener">
        <div className="course-element1 ele">
          <h2>Podologia</h2>
          <ul>
            <li>
              Wykorzystanie: skalpela, duta, hebla, kapturkw i pododyskw w
              pracy podologa
            </li>
            <li>
              Korekta wrastajcych paznokci : klamra tytanowa, tamponada i inne
              metody korekty
            </li>
            <li>
              Usuwanie odciskw i modzeli mechaniczne ,chemiczne zapobieganie
              powstaniu
            </li>
            <li>Choroby i defekty stp i paznokci </li>
            <li>Pkajce pity</li>
            <li>Stopa cukrzycowa</li>
          </ul>
        </div>
        <div className="course-element2 ele">
          <h2>Kosmetologia estetyczna</h2>
          <ul>
            <li>
              Mezoterapia igowa i mikroigowa jako metody stymulacji /produkty
              i technika/
            </li>
            <li>
              Zastosowanie peelingw chemicznych o rnej intensywnoci w
              zabiegach pielgnacyjnych i anti aging/ kwasy, TCA, reinole,
              witamina c i inne produkty i technika zabiegowa/
            </li>
            <li>
              Stymulatory tkankowe/ praca ig i kaniul ,produkty i technika
              zabiegowa/
            </li>
            <li>Osocze bogatopytkowe i fibryna</li>
          </ul>
        </div>
        <div className="course-element3 ele">
          <h2>Oprawa oczu</h2>
          <ul>
            <li>
              Henna klasyczna, farbki, henna pudrowa, henna elowa, geometria
              brwi
            </li>
          </ul>
        </div>
        <div className="course-element4 ele">
          <h2>Masae grupy max 4 osobowe</h2>
          <ul>
            <li>Kobido</li>
            <li>Masa gorcymi kamieniami</li>
            <li>Masa karku i plecw</li>
            <li>Masa antycelulitowy</li>
            <li>Zabiegi pielgnacyjna na ciao</li>
            <li>Akupunktura kosmetyczna</li>
            <li>Masa Gua Sha</li>
          </ul>
        </div>
        <div className="course-element5 ele">
          <h2>Szkolenia eksperckie</h2>
          <ul>
            <li>
              Oczyszczanie manualne w terapiach anti acne i terapii
              pielgnacyjnej
            </li>
            <li>Sanepid w gabinecie kosmetycznym</li>
          </ul>
        </div>
      </article>
      <Footer />
    </section>
  );
}
