import { quotesLeft } from "react-icons-kit/icomoon/quotesLeft";
import Icon from "react-icons-kit";

const Testimonials = () => {
  return (
    <section className="testimonial-container">
      <header className="section-heading">
        <h1>Co mwi o nas klienci</h1>
      </header>
      <article className="testimonials">
        <TestimonialItem
          name="Anna Wielgus-Basta"
          job="Kosmetolog - szkoleniowiec"
          quote="Nela, jako: fachowiec, kosmetolog, szkoleniowiec pracuje na najwyszym poziomie.
                miem twierdzi e uzyskaa taki profesjonalizm jaki sama chciaabym mie. Ogromna wiedza merytoryczna i praktyczna. Przejrzysto wypowiedzi, opanowanie, rzetelno, komunikatywno i dostpno. Nie ma pyta bez odpowiedzi. Cierpliwo i bardzo dokadne przystosowanie si do potrzeb klienta. Osobicie darz ogromn sympati i szacunkiem Pani Nel i bardzo sobie ceni sposb w jaki pracuje i jak zaraa wiedz. Na szkoleniach jest atmosfera sprzyjajca przyswojeniu wiedzy i umiejtnoci"
        />
        <TestimonialItem
          name="Agnieszka Figalska"
          job="Coach - Trener-Nauczyciel"
          quote="Nela, to wspaniay czowiek, wysokiej klasy specjalista i mistrzyni w swoim fachu. Podchodzi do klienta caociowo, ma ogromna wiedze a przy tym i pasj do wykonywania swojego zawodu. Czujesz si przy niej jak przy czytaniu ksiki - im duej j czytasz tym bardziej jest dla ciebie interesujca. Nie bez powodu jest szkoleniowcem. Wszechstronna wiedza i wspaniae rce powoduj e czujesz si bezpiecznie podczas wykonywanych przez Nel zabiegw. Dowiadczenie, umiejtnoci, wczucie si w potrzeby s bezcenne kiedy czowiek sam nie wie od czego zacz i na co si zdecydowa w szerokiej oferci.
          Gorco polecam usugi Neli"
        />
        <TestimonialItem
          name="Gabriela Grny"
          job="Klient"
          quote="Spotkaam kobiet niezwyk: wulkan pomysw i nowych rozwiza w kosmetyce, ocean pogodnoci i uwanoci. Kady zabieg u Neli zmienia si w ciekaw pen inspiracji rozmow. Czsto potem myl czy w innym gabinecie czuabym si fajnie....Chyba nie"
        />
      </article>
    </section>
  );
};

const TestimonialItem = (props) => {
  return (
    <div className="testimonial-item">
      <div className="inner-content">
        <Icon icon={quotesLeft} size={30}></Icon>
        <h1>{props.name}</h1>
        <h2>{props.job}</h2>
        <p>{props.quote}</p>
      </div>
    </div>
  );
};

export default Testimonials;
