import me from "../images/nela/nela.jpg";
import me1000 from "../images/nela/nela-1000.jpg";
import me1000webp from "../images/nela/nela-1000.webp";
import mewebp from "../images/nela/nela.webp";
import Footer from "../components/Footer";

export default function About() {
  return (
    <div className="about-wraper">
      <picture className="photo">
        <source media="(max-width: 1000px)" srcset={me1000} />
        <source media="(max-width: 1000px)" srcset={me1000webp} />
        <source media="(min-width: 1001px)" type="image/jpg" srcset={me} />
        <source media="(min-width: 1001px)" type="image/webp" srcset={mewebp} />
        <img src={me} alt="My photo" loading="lazy" />
      </picture>
      <div className="about">
        <h1>Nela Tyszkiewicz</h1>
        <p>
          Brana kosmetyczna zmienia si waciwie z miesica na miesic. Na
          rynku pojawiaj si kolejne propozycje, by zatrzyma proces starzenia
          skry. Gdy ja zaczynaam prac w zawodzie, miaam do dyspozycji raptem
          kilka metod dbania o wygld. Dzisiaj sama szkol i uywam przynajmniej
          kilkunastu. Bo wspczesna kosmetolog jest nie tylko kim, kto potrafi
          wykona nawet najbardziej skomplikowany zabieg, ale te kim, kto
          potrafi prowadzi klientk mdrze i skutecznie planujc pielgnacje
          jej skry nawet kilka miesicy z wyprzedzeniem. Dlatego tak wielk
          wag przywizuje do tego, by dokadnie wytumaczy klientce, po co
          wykonuj dany zabieg i jakie bdzie mia dugoterminowe skutki.
          <br></br>
          <br></br>
          Wspczesna kobieta potrzebuje przede wszystkim zaufania do swojego
          kosmetologa. Odda si w nasze rce z wiar, e to, co robimy ma sens
          i e warto w to inwestowa. Przy takiej iloci ofert, wiele pa czuje
          si zagubionych i skacz z zabiegu na zabieg. Albo lepo naladuj
          twarze pokazywane w mediach spoecznociowych. To nie moje mylenie.
          Ja patrz na kobiet, sucham jej historii i stylu ycia i przekonuj
          do podjcia odpowiednich krokw. Czasem zuszczamy. Czasem nawilamy.
          Czasem ujdrniamy. Najwaniejsze, by kolejne kroki wynikay jeden z
          drugiego. Innymi sowy, by dbanie miao sens.
          <br></br>
          <br></br>
          Jestem szkoleniowcem, nauczycielem I wykadowc. Szkol z caej gamy
          zabiegw. Ucz procedur zabiegowych z wykorzystaniem: chemicznych
          peelingw, mezoterapii igowej i mikroigowej, stymulatorw
          tkankowych, masay liftingujcych w tym KOBIDO oraz tworzenia
          holistycznych procedur zabiegowych. Rwnoczenie ca wiedz posuguj
          si w pracuj jako kosmetolog. Kada osoba odwiedzajca mj gabinet
          moe mie pewno e osignie optymalne korzyci z zabiegw i terapii
          tu wykonywanych. <br></br>
          <br></br>Serdecznie zapraszam - Nela Tyszkiewicz
        </p>
      </div>
      <Footer />
    </div>
  );
}
