import React, { useState } from "react";
import { smallRight } from "react-icons-kit/entypo/smallRight";
import { smallDown } from "react-icons-kit/entypo/smallDown";
import { Icon } from "react-icons-kit";
import Footer from "../components/Footer.js";

const Services = () => {
  const [servicesList] = useState([
    {
      id: 1,
      name: "Zabieg nawilajcy",
      description:
        "Dwuetapowe oczyszczanie skry twarzy, peeling kawitacyjny lub enzymatyczny, masa z wykorzystaniem produktw nawilajcych, stabilizujca maska algowa",
    },
    {
      id: 2,
      name: "Zabieg rewitalizujcy",
      description:
        "Dwuetapowe oczyszczanie skry twarzy, peeling kawitacyjny/enzymatyczny, masa relaksacyjny z wykorzystaniem produktw rewitalizacyjnych, stabilizujca maska algowa",
    },
    {
      id: 3,
      name: "Zabieg liftujcy",
      description:
        "Dwuetapowe oczyszczanie skry twarzy, peeling kawitacyjny/Combo Peptide, masa liftingujcy, stabilizujca maska algowa",
    },
    {
      id: 4,
      name: "Henna",
      description:
        "Klasyczny zabieg farbowania brwi i rzs. W zalenoci od upodoba klientki moe by wykonany przy uyciu henny proszkowej, pudrowej, elowej, kremowej czy popularnej obecnie farbki. Kolorystyka wacha si od granatowej czerni poprzez  popieli do rnych odcieni brzu. Cena wacha si od 30z do 130z w zalenoci od techniki.",
    },
    {
      id: 5,
      name: "Pedicure leczniczy",
      description:
        "Wykonany przy uyciu ostrzy i frezarki. Obejmuje korekt paznokci, opracowanie zrogowace , pknitych pit, usunicie odciskw i modzeli",
    },
    {
      id: 6,
      name: "Pedicure hybrydowy i kosmetyczny",
      description:
        "Pedicure hybrydowy - klasyczny zabieg pielgnacyjno-upikszajcy polegajcy na kosmetycznym usuniciu zrogowaciaego, szorstkiego naskrka, wyprofilowaniu pytki paznokciowej i pomalowaniu wybranym lakierem hybrydowym. Pedicure kosmetyczny - klasyczny zabieg pielgnacyjny przywracajcy stop mikko i gadko. Polega na usuniciu szorstkoci na pietach i uformowaniu paznokci.",
    },
    {
      id: 7,
      name: "Peelingi chemiczne",
      description:
        "Niezwykle skuteczny zabieg ukierunkowany na potrzeby skry. Dziki waciwie dobranym i wykonanym procedur mona uzyska : odmodzenie i oczyszczenie twarzy, wyrwnanie kolorytu, nawilenie, ujdrnienie, redukcj drobnych zmarszczek i plam pigmentacyjnych. Najczciej stosowanie w zabiegach peelingi to: kwas migdaowy, retinol, witamina C, TCA ",
    },
    {
      id: 8,
      name: "Mezoterapia igowa",
      description:
        "Zabieg wykonywany preparatami odpowiednimi do potrzeb skry. Produkty s certyfikowane. Zabieg moe by wykonywany na twarzy, szyi, dekolcie i skrze gowy. Gwarantujemy najwysz dokadno wykonania. Przykadowe koktajle to : RevitaCare / Cytocare / Fillmed / Filorga / Venoma. Dr Cyj i inne",
    },
    {
      id: 9,
      name: "Mezotewrapia mikroigowa",
      description:
        "Bezpieczny zabieg niechirurgicznej rewitalizacji skry, stymuluje naturalny proces odnowy, poprawia jdrno, koloryt i tworzenie nowych naczy wosowatych. Zabieg mona czy z innymi zabiegami",
    },
    {
      id: 10,
      name: "Mezoterapia hybrydowa",
      description:
        "Zoony zabieg kosmetologiczny polegajcy na poczeniu dwch technik: mezoterapii igowej i mikroigowej. W zalenoci od wykorzystanych preparatw mona precyzyjnie celowa w potrzeby skry.",
    },
    {
      id: 11,
      name: "Stymulatory Tkankowe",
      description:
        "Najnowsze zabiegi poprawiajce wygld skry. Opniajce proces starzenia, Preparaty pobudzaj produkcj kolagenu i elastyny. Dodaj skrze wieoci i promiennoci. Posiadaj silne dziaanie napinajce redukuj przebarwienia. Daj lekki efekt wypenienia. Dziaaj intensywne waciwoci odbudowujce. Gwarantujemy preparaty tylko sprawdzone z odpowiednimi certyfikatami .Preparat dobierany jest indywidualnie do potrzeb skry. Wysoka precyzja wykonania. Przykadowe produkty to: Jalupro, MonoDerm Fillagen, Sisthaema, Phormae, Venome, Karisma, Ejal 40, Nucleofill, Vitaran. EGZOSOMY - Pionierski zabieg kosmetologii estetycznej z wykorzystaniem nanopchechykw zewntrz komrkowych- EGZOSOMW. Egzosomy modeluj regeneruj,odmadzaj i rewitalizuja tkanki. S biodostpne i biostabilne. Dziaaja przeciwzapalnie indukuj syntez kolagenu i elastyny, przyspieszaj gojenie si ran.",
    },
    {
      id: 12,
      name: "Masae",
      description:
        "Zabiegi wykonywane na produktach Marki MENARD. Kobido - tradycyjny masa dalekowschodni tkanek gbokich, powizi mini i skry. Silnie liftingujce dziaanie. poprawia ukrwienie, odpr ,usprawnia przepywy naczyniowe, relaksuje, poprawia koloryt skry. Gua Sha - Zabieg dla wymagajcych wykonywany kamieniami jadeitowymi/kwarcowymi/ W zalenoci od potrzeb energetycznych klienta. Pobudza, drenuje, dotlenia, uruchamia proces detoksykacji, rozlunia, Kamienie jednorazowe",
    },
    {
      id: 13,
      name: "Mikropunktura",
      description:
        "Zabieg polegajcy na przezskrnym indukowanie kolagenu. Dziki unikatowej technice pracy mona wpyn na skr  odmadzajco i rewitalizujco /biolifting/ oraz wspomaga redukcj  blizn o rnym pochodzenu  /Korekta blizn : pooperacyjnych, po trdzikowych, rozstpw/ ",
    },
    {
      id: 14,
      name: "Akupunktura kosmetyczna",
      description:
        "Natutalny sposb na zatrzymanie modoci. Efekt rewitalizacji, rozjanienie i poprawa ukrwienia skry/mona poczy z innymi zabiegami",
    },
  ]);

  const [selectedService, setSelectedService] = useState(null);

  const toggleService = (id) => {
    if (selectedService === id) {
      setSelectedService(null);
    } else {
      setSelectedService(id);
    }
  };

  return (
    <section className="services-section">
      <article className="services">
        {servicesList.map((service) => (
          <div
            key={service.id}
            className="toggle-list-element"
            onClick={() => toggleService(service.id)}
          >
            <div className="up">
              <h2 className="toggle-list-title animacja">
                {selectedService === service.id ? (
                  <Icon size={25} icon={smallDown}></Icon>
                ) : (
                  <Icon size={25} icon={smallRight}></Icon>
                )}{" "}
                {service.name}
              </h2>
            </div>
            {selectedService === service.id && (
              <p className="toggle-list-description">{service.description}</p>
            )}
          </div>
        ))}
      </article>
      <Footer />
    </section>
  );
};

export default Services;
