import Footer from "../components/Footer";
import { motion } from "framer-motion";

export default function Pricing() {
  return (
    <div className="wrap-pricing">
      <h1>Cennik*</h1>

      <div className="container-pricing">
        <p>
          *Powysza oferta ma charakter informacyjny i nie stanowi oferty
          handlowej w rozumieniu art.66 Paragraf 1 Kodeksu cywilnego. W celu
          weryfikacji ceny prosz o kontakt.
        </p>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Kosmetologia klasyczna</h2>
          <div className="row">
            <div className="title">
              <h3>ZABIEG NAWILAJCY</h3>
            </div>
            <div className="price">180z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>ZABIEG REWITALIZUJCY</h3>
            </div>
            <div className="price">249z</div>
            {/* <div className="price">
                        <div className="options">1ml 700 z</div>
                        <div className="options">2ml 1300 z</div>
                        <div className="options">6/7ml 2500 z</div>
                    </div> */}
          </div>
          <div className="row">
            <div className="title">
              <h3>ZABIEG LIFTINGUJCY</h3>
              <p>
                W skad zabiegu wchodzi: Oczyszczenie, Peeling, Aplikacja
                odpowiedniego do potrzeb skry preparatu, Masa, Maska
                odpowiednia do potrzeb skry
              </p>
            </div>
            <div className="price">259z</div>
          </div>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Peelingi Chemiczne</h2>
          <div className="row">
            <div className="title">
              <h3>Retix C</h3>
            </div>
            <div className="price">280z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>PQ AGE</h3>
            </div>
            <div className="price">340z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>BioRePeel</h3>
            </div>
            <div className="price">300z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>TCA 40%</h3>
            </div>
            <div className="price">350z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>PRX-T33</h3>
            </div>
            <div className="price">360z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>PQAg Evolution</h3>
            </div>
            <div className="price">395z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Retinol 5%</h3>
            </div>
            <div className="price">190z</div>
          </div>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Kosmetologia estetyczna</h2>
          <div className="row">
            <div className="title">
              <h3>Mezoterapia mikroigowa</h3>
            </div>
            <div className="price">280z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Mezoterapia igowa</h3>
              <p>Cena w zalenoci od preparatu i obszaru</p>
            </div>
            <div className="price">320 - 550z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Stymulatory tkankowe i egzosomy</h3>
            </div>
            <div className="price">600 - 1400z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Mezoterapia hybrydowa</h3>
            </div>
            <div className="price">280 - 480z</div>
          </div>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Masae twarzy</h2>
          <div className="row">
            <div className="title">
              <h3>Kobido</h3>
            </div>
            <div className="price">160z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Liftingujcy twarz</h3>
            </div>
            <div className="price">140z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Masa relaksacyjny</h3>
            </div>
            <div className="price">120z</div>
          </div>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Podologia</h2>
          <div className="row">
            <div className="title">
              <h3>Pedicur klasyczny</h3>
            </div>
            <div className="price">120z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Pedicur hybrydowy</h3>
            </div>
            <div className="price">140z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Pedicur leczniczy</h3>
            </div>
            <div className="price">od 140z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Usuwanie odciskw</h3>
            </div>
            <div className="price">od 60z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Klamry tytanowe na wrastajce paznokcie</h3>
            </div>
            <div className="price">150z</div>
          </div>
        </motion.div>
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
        >
          <h2 className="main-title">Henny i Laminacja</h2>
          <div className="row">
            <div className="title">
              <h3>Klasyczna henna - komplet</h3>
            </div>
            <div className="price">45z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Brwi</h3>
            </div>
            <div className="price">25z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Regulacja brwi</h3>
            </div>
            <div className="price">20z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Rzsy</h3>
            </div>
            <div className="price">30z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Henna pudrowa i geometria</h3>
            </div>
            <div className="price">140z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Laminacja brwi</h3>
            </div>
            <div className="price">120z</div>
          </div>
          <div className="row">
            <div className="title">
              <h3>Laminacja rzs</h3>
            </div>
            <div className="price">130z</div>
          </div>
        </motion.div>
      </div>
      <Footer />
    </div>
  );
}
